.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
html, body, #viewport {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100vh;
    padding: 0;
    font-family: sans-serif;
    text-align: center;
    user-select: none;
}
#bingo-card {
  display: grid;
  top: 0px;
  background-image: url('./Background images/GilligansGraveLogo full height.png');
      background-size: contain;
background-repeat: no-repeat;
grid-template: repeat(5, 18vmin) /repeat(5, 18vmin);
grid-auto-flow: row;
  /* box-shadow: 0 0 1vmin #000;
